import { Component, OnInit, ViewChild } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-upload-picture',
  templateUrl: './upload-picture.component.html',
  styleUrls: ['./upload-picture.component.css']
})
export class UploadPictureComponent implements OnInit {
  @ViewChild('myURLInput')
  myLocalInputVariable: any;

  @ViewChild('myURLInput2')
  myLocalInputVariable2: any;

  @ViewChild('myLocalInput')
  myLocalInputVariable3: any;

  @ViewChild('myInput2')
  myLocalInputVariable4: any;

  public imageStyle1 = 'assets/art1.jpg';
  public imageStyle2 = 'assets/art2.jpg';
  public imageStyle3 = 'assets/convert-steps/step2.jpg';

  image1: any;
  image2: any;
  image3: any;
  myURL: any;
  myURL2: any;
  message: string;
  message2: string;

  isLoading = false;

  selectedFile = null;
  toogleBool = true;
  toogleBool2 = true;

  constructor(private http: HttpClient) {}

  reset() {
    console.log(this.myLocalInputVariable.nativeElement.files);
    this.myLocalInputVariable.nativeElement.value = '';
    console.log(this.myLocalInputVariable.nativeElement.files);
  }

  reset2() {
    console.log(this.myLocalInputVariable2.nativeElement.files);
    this.myLocalInputVariable2.nativeElement.value = '';
    console.log(this.myLocalInputVariable2.nativeElement.files);
  }
  reset3() {
    console.log(this.myLocalInputVariable3.nativeElement.files);
    this.myLocalInputVariable3.nativeElement.value = '';
    console.log(this.myLocalInputVariable3.nativeElement.files);
  }
  reset4() {
    console.log(this.myLocalInputVariable4.nativeElement.files);
    this.myLocalInputVariable4.nativeElement.value = '';
    console.log(this.myLocalInputVariable4.nativeElement.files);
  }

  onURLinserted() {
    this.getImage(this.myURL).subscribe(
      data => {
        this.createImageFromBlob(data);
      },
      error => {
        console.log('Error occured', error);
      }
    );
  }

  getImage(imageUrl: string): Observable<Blob> {
    return this.http.get(imageUrl, { responseType: 'blob' });
  }

  createImageFromBlob(image: Blob) {
    const reader = new FileReader();
    reader.addEventListener(
      'load',
      () => {
        this.image1 = reader.result; // here is the result you got from reader
      },
      false
    );

    if (image) {
      this.selectedFile = image;
      reader.readAsDataURL(image);
      this.toogleBool = false;
      this.reset3();
      console.log(image);
    }
  }

  ngOnInit() {
    this.isLoading = true;
  }

  readUrl(event: any) {
    this.selectedFile = <File>event.target.files[0];

    if (event.target.files && this.selectedFile) {
      const reader = new FileReader();

      reader.onload = (event1: any) => {
        this.image1 = event1.target.result;
        this.toogleBool = false;
      };

      reader.readAsDataURL(this.selectedFile);
      console.log(event);
      this.reset();
    }
  }

  readUrl2(event: any) {
    this.selectedFile = <File>event.target.files[0];

    if (event.target.files && this.selectedFile) {
      const reader = new FileReader();

      reader.onload = (event1: any) => {
        this.image2 = event1.target.result;
        this.toogleBool2 = false;
      };

      reader.readAsDataURL(this.selectedFile);
      console.log(event);
      this.reset2();
    }
  }

  onUpload() {
    const fd = new FormData();
    fd.append('image', this.selectedFile, this.selectedFile.name);
    this.http
      .post('http://localhost:3000/uploadhandler', fd, {
        reportProgress: true,
        observe: 'events'
      })
      .subscribe(event => {
        return (this.message = 'Upload successful');
      });
  }

  onUpload2() {
    const fd = new FormData();
    fd.append('image', this.selectedFile, this.selectedFile.name);
    this.http
      .post('http://localhost:3000/uploadhandler', fd, {
        reportProgress: true,
        observe: 'events'
      })
      .subscribe(event => {
        return (this.message2 = 'Upload successful');
      });
  }

  onURLinserted2(image: any) {
    console.log(image);
    this.getImage2(image).subscribe(
      data => {
        this.createImageFromBlob2(data);
      },
      error => {
        console.log('Error occured', error);
      }
    );
  }

  getImage2(imageUrl: string): Observable<Blob> {
    return this.http.get(imageUrl, { responseType: 'blob' });
  }

  createImageFromBlob2(image: Blob) {
    const reader = new FileReader();
    reader.addEventListener(
      'load',
      () => {
        this.image2 = reader.result; // here is the result you got from reader
      },
      false
    );

    if (image) {
      this.selectedFile = image;
      reader.readAsDataURL(image);
      this.toogleBool = false;
      console.log(image);
      this.reset4();
    }
  }

  changeMainImg(image: any) {
    this.image2 = image;
    this.selectedFile = image;

    console.log(this.selectedFile);
    console.log(this.selectedFile.name);
  }
}
